import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Index from './views/Index.vue'
import Home from './views/Home.vue'
import Form from './views/nav1/Form.vue'
import Page5 from './views/nav2/AdminInfo.vue'
import SysLog from './views/nav3/SysLog.vue'
import AdminLog from './views/nav3/AdminLog.vue'
import UserLog from './views/nav3/UserLog.vue'
import ProtocolConfig from './views/nav4/ProtocolConfig.vue'
import User from "./views/nav2/User";

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        leaf: true,//
        children: [
            {   path: '/Index',
                component: Index,
                name: ''
            }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-loading',
        leaf: true,
        children: [
            // { path: '/user', component: user, name: '4' },
            { path: '/page5', component: Page5, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-view',//class
        leaf: true,//
        children: [
            // { path: '/main', component: Main, name: '', hidden: true },
            { path: '/User', component: User, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-setting',//class
        leaf: true,//
        children: [
            { path: '/ProtocolConfig', component: ProtocolConfig, name: '' }
        ]
    },

    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/SysLog', component: SysLog, name: '' },
            { path: '/AdminLog', component: AdminLog, name: '' },
            { path: '/UserLog', component: UserLog, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            // { path: '/page4', component: Page4, name: '' },
            // { path: '/Main', component: Main, name: '' },
            { path: '/Form', component: Form, name: '' }
        ]
    },

    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


